/* !Basics */

*{
  box-sizing: border-box;
  margin: 0;
}
body {
    font-family: 'Major Mono Display', monospace;
    font-size: 1em;
    background-image: url(../public/nasaCluster.jpg);
    background-size: 36vmax;
    padding: 0.4em;

  }

  .flex-grid{
    display: flex;
    margin: 1em;
    padding: 0.4em;
  }
  .flex-grid .col {
    flex: 1;
  }

  .nav-link{
    color:#ff8400;
    /* opacity: 110%; */
  }
  #title{
    color: rgb(2, 1, 1);
  }
  /* !navbar styling */
#ie4 {
    height: 1.4em;
    width: 100%;
    display: flex;
    align-items: center;
    position: relative;
    padding: 0.5rem 0rem;
    color: #F8F8FF;
/* https://blog.logrocket.com/create-responsive-navbar-react-css/ */
    margin-left: auto;
    box-shadow: 0 2px 2px 2px rgba(9,9,9,0.2);
    font-size: 2em;
    font-weight: bolder;
    list-style: none;
    background-color: #305040;
  }
  #ie4 ul{
    display: flex;
    gap:1rem;
    /* gap: 1rem; */
  }
  #ie4 a{
    text-decoration: none;
    /* margin: 0.5rem; */
    /* margin-left: auto; */
    padding: 0.5em;
  }

  #landingPage{
    font-size: 0.9em;
    /* font-family: 'Major Mono Display', monospace; */
    font-weight: bolder;
    color: #ff9914;
    /* background-color: #abc3e2; */
    font-weight: 1444;
    margin-bottom: 1rem;
    padding: 0 2em;
  }
  #revealer{
    font-family: 'Major Mono Display', monospace;
    font-weight: 1444;
    color: #ff9914;
    /* background-color: #b68a5c; */
    margin-top: 1rem;
  }

  footer{
    /* font-family: 'Major Mono Display', monospace; */
    color: #ff9914;
    font-weight: 1404;
    /* background-color: #b68a5c; */
    margin-top: 20px;
    font-size: 0.8em;
  }
  input{
    /* font-family: 'Major Mono Display', monospace; */
    color: #ff9914;
    font-weight: 1000;
    margin-top: 20px;
  }
  #pictureRef a{
    color: #ff9914;
  }
div > p{
  opacity: 200%;
  font-weight: bolder;
}